<template>
  <div class="dashboard-page-chart">
    <div class="dashboard-page-chart__body">
      <b-row class="dashboardBody">
        <b-col md="12">
          <div class="card">
            <div class="card__title">
              <b-row>
                <b-col style="max-width: max-content !important;">
                  <span>Fleet Maintenance Status</span>
                </b-col>
                <b-col style="text-align: right;">
                  <ABSButton
                    :text="'Back'"
                    classButton="button button--back"
                    classIcon="icon-style-1"
                    @click="doBack"
                  />
                </b-col>
              </b-row>
            </div>
            <div class="card__body">
              <b-form
                :data-vv-scope="'MM_FleetMaintenanceSchedule'"
                :data-vv-value-path="'MM_FleetMaintenanceSchedule'"
              >
                <b-row>
                  <b-col md="2" style="text-align: center;">
                    <img :src="require('@/assets/paper.png')" alt style="width: 50px;" />
                  </b-col>
                  <b-col md="10">
                    <b-row style="margin-bottom: 10px;">
                      <b-col style="padding-left: 0px !important;">
                        <b-row
                          style="border-top: solid 1px #e7eaec;border-left: solid 1px #e7eaec;border-right: solid 1px #e7eaec; border-radius: 5px 5px 0px 0px;"
                        >
                          <b-col
                            style="border-right: solid 1px #e7eaec; padding: 10px !important; max-width: 20% !important;"
                          >
                            <span class="lbl-view">Fleet No</span>
                            <br />
                            <span>{{Model.fleet_no}}</span>
                          </b-col>
                          <b-col
                            style="border-right: solid 1px #e7eaec; padding: 10px !important; max-width: 20% !important;"
                          >
                            <span class="lbl-view">Fleet Type</span>
                            <br />
                            <span>{{Model.fleet_type}}</span>
                          </b-col>
                          <b-col
                            style="border-right: solid 1px #e7eaec; padding: 10px !important; max-width: 20% !important;"
                          >
                            <span class="lbl-view">Driver</span>
                            <br />
                            <span>{{Model.driver}}</span>
                          </b-col>
                          <b-col
                            style="border-right: solid 1px #e7eaec; padding: 10px !important; max-width: 20% !important;"
                          >
                            <span class="lbl-view">Maintenance Type</span>
                            <br />
                            <span>{{Model.maint_type}}</span>
                          </b-col>
                          <b-col
                            style="border-right: solid 1px #e7eaec; padding: 10px !important; max-width: 20% !important;"
                          >
                            <span class="lbl-view">Maintenance Status</span>
                            <br />
                            <span>{{Model.status}}</span>
                          </b-col>
                        </b-row>
                        <b-row style="border: solid 1px #e7eaec;border-radius: 0px 0px 5px 5px;">
                          <b-col
                            style="border-right: solid 1px #e7eaec; padding: 10px !important; max-width: 20% !important;"
                          >
                            <span class="lbl-view">Last Maintenance</span>
                            <br />
                            <span>{{momentDateFormatting(Model.last_maint, "DD/MM/YYYY")}}</span>
                          </b-col>
                          <b-col
                            style="border-right: solid 1px #e7eaec; padding: 10px !important; max-width: 20% !important;"
                          >
                            <span class="lbl-view">Last Millage</span>
                            <br />
                            <span>{{isCurrency(Model.last_millage, 0)}}</span>
                          </b-col>
                          <b-col
                            style="border-right: solid 1px #e7eaec; padding: 10px !important; max-width: 20% !important;"
                          >
                            <span class="lbl-view">Current Millage</span>
                            <br />
                            <span>{{isCurrency(Model.current_millage, 0)}}</span>
                          </b-col>
                          <b-col
                            style="border-right: solid 1px #e7eaec; padding: 10px !important; max-width: 20% !important;"
                          >
                            <span class="lbl-view">Fleet Status</span>
                            <br />
                            <span>{{Model.fleet_status}}</span>
                          </b-col>
                          <b-col
                            style="border-right: solid 1px #e7eaec; padding: 10px !important; max-width: 20% !important;"
                          >
                            <span class="lbl-view">Estimated Available Time</span>
                            <br />
                            <span>{{Model.estimated_avail_date}}</span>
                          </b-col>
                        </b-row>
                      </b-col>
                    </b-row>
                    <b-row style="margin-bottom: 10px;" v-if="Model.schedule_status != 'P'">
                      <b-col md="6">
                        <span>
                          <label><span style="color:red;">* </span>{{titleSchedule}}</label>
                        </span>
                        <ACCRadioButton
                          @input="OnMaintenanceStatusChange"
                          :prop="PI_maintenance_status"
                          v-model="Model.maintenance_status"
                          ref="ref_maintenance_status"
                        />
                      </b-col>
                    </b-row>
                    <b-row style="margin-bottom: 10px;" v-if="Model.schedule_status == 'U' || Model.schedule_status == 'S'">
                      <b-col md="6">
                        <span>
                          <label><span style="color:red;" v-if="PI_maintenance_date.cValidate == 'required'">* </span>{{titleDate}}</label>
                        </span>
                        <ACCDateTime
                          @input="OnMaintenanceDate"
                          :prop="PI_maintenance_date"
                          v-model="Model.maintenance_date"
                          ref="ref_maintenance_date"
                        />
                      </b-col>
                    </b-row>
                    <b-row style="margin-bottom: 10px;" v-if="Model.schedule_status == 'Q'">
                      <b-col md="6">
                        <span>
                          <label><span style="color:red;" v-if="PI_reason_code.cValidate == 'required'">* </span>Reason Code</label>
                        </span>
                        <ACCDropDown
                          @change="OnReasonCodeChange"
                          :prop="PI_reason_code"
                          v-model="Model.reason_code"
                          :label="Model.reason_codeLabel"
                          ref="ref_reason"
                        />
                      </b-col>
                    </b-row>
                    <b-row style="margin-bottom: 10px;" v-if="Model.schedule_status == 'Q'">
                      <b-col md="6">
                        <span>
                          <label>Reason</label>
                        </span>
                        <ACCTextArea :prop="PI_reason" v-model="Model.reason" ref="ref_reason" />
                      </b-col>
                    </b-row>
                    <b-row style="margin-bottom: 10px;" v-if="Model.schedule_status != 'P'">
                      <b-col md="6">
                        <ABSButton
                          :text="'Confirm'"
                          classButton="btn btn--default"
                          classIcon="icon-style-default"
                          @click="doSave"
                          styleButton="height: 40px;width: 100%;"
                        />
                      </b-col>
                    </b-row>
                    <b-row style="margin-bottom: 10px;" v-if="Model.schedule_status == 'P'">
                      <b-col md="3" style="padding: unset !important;">
                        <b-row
                          style="border: solid 1px #e7eaec; border-radius: 5px 5px 0px 0px;"
                        >
                          <b-col style="padding: 10px !important;">
                            <span class="lbl-view">Estimated Finish Date</span>
                            <br />
                            <span>{{Model.estimated_finish_date}}</span>
                          </b-col>
                        </b-row>
                      </b-col>
                    </b-row>
                  </b-col>
                </b-row>
                <div class="card" v-for="(data, index) in detail_days" style="margin-bottom: 10px;">
                  <div class="card__title">
                    <b-row>
                      <b-col style="max-width:max-content !important;">
                        <span>{{momentDateFormatting(new Date(Date.now() + (3600 * 1000 * 24 * index)), "dddd, DD MMM YYYY")}}</span>
                      </b-col>
                      <b-col style="text-align: right;">
                        <span style="color: grey;">
                          <b-badge variant="danger">{{data[0].total_unsch}}</b-badge>
                          Unschedule&nbsp;&nbsp;

                          <b-badge variant="magenta">{{data[0].total_sch}}</b-badge>
                          Scheduled&nbsp;&nbsp;
                          
                          <b-badge variant="primary">{{data[0].total_queue}}</b-badge>
                          Queued Up&nbsp;&nbsp;
                          
                          <b-badge variant="success">{{data[0].total_on_progress}}</b-badge>
                          On Progress&nbsp;&nbsp;&nbsp;
                        </span>
                        <span>
                          <ABSButton
                            :text="'View All'"
                            classButton="button button--back"
                            classIcon="icon-style-1"
                            @click="doViewAll(index)"
                          />
                        </span>
                      </b-col>
                    </b-row>
                  </div>
                  <div class="card__body">
                    <div>
                      <ACCFormList
                        :prop="{}"
                        :title="''"
                        cStatic
                        cClass="table-style-3_noAct"
                        :cHeader="scheduleHeader"
                        :cData="data"
                        @rowClicked="rowClicked"
                        ref="ref_list"
                      >
                        <template slot="last_maint" slot-scope="data">
                          <span>{{momentDateFormatting(data.item.last_maint, "DD/MM/YYYY")}}</span>
                        </template>
                        <template slot="last_millage" slot-scope="data">
                          <span>{{isCurrency(data.item.last_millage, 0)}}</span>
                        </template>
                        <template slot="current_millage" slot-scope="data">
                          <span>{{isCurrency(data.item.current_millage, 0)}}</span>
                        </template>
                        <template slot="usage" slot-scope="data">
                          <span>{{isCurrency(data.item.usage, 0)}}</span>
                        </template>
                      </ACCFormList>
                    </div>
                  </div>
                </div>
              </b-form>
            </div>
          </div>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      Model: {
        current_millage: 0,
        detail_day1: [],
        detail_day2: [],
        detail_day3: [],
        driver: "",
        estimated_avail_date: "",
        fleet_no: "",
        fleet_status: "",
        fleet_type: "",
        fm_driver_id: 0,
        fm_fleet_mstr_id: 0,
        fm_fleet_type_id: 0,
        last_maint: "",
        last_millage: 0,
        lastupdatestamp: 0,
        maint_type: "",
        mm_maintenance_type_id: 0,
        row_id: 0,
        schedule_date: "",
        schedule_status: "",
        ss_portfolio_id: 0,
        status: "",
        time_edit: "",
        total_on_progress: 0,
        total_queue: 0,
        total_sch: 0,
        total_unsch: 0,
        usage: 0,

        maintenance_status: "",
        maintenance_date: "",
        reason_code: "",
        reason_codeLabel: "",
        reason: "",
      },
      titleSchedule: "",
      titleDate: "",
      detail_days: [],
      PI_maintenance_status: {
        cValidate: "required",
        cName: "maintenance_status",
        cId: "rdmaintenance_status",
        cRadioOptions: [
          { text: "Yes", value: "Y" },
          { text: "No", value: "N" }
        ],
        cRadioDefaultOption: "",
        cOrder: 1,
        cProtect: false,
        cVisible: true,
        cGroup: false,
        cParentForm: "MM_FleetMaintenanceSchedule",
        cInputStatus: this.inputStatus,
      },
      PI_maintenance_date: {
        cValidate: "required",
        cName: "date",
        cOrder: 2,
        cKey: false,
        cProtect: false,
        cWithTime: false,
        cFormat: "dd/MM/yyyy",
        cParentForm: "MM_FleetMaintenanceSchedule",
        cInputStatus: this.inputStatus,
      },
      PI_reason_code: {
        dataLookUp: {
          LookUpCd: "GetCmReason",
          ColumnDB: "reason_cd",
          InitialWhere: "ss_portfolio_id='" + this.getDataUser().portfolio_id + "'",
          ParamWhere: "",
          OrderBy: "",
          ParamView: "",
          SourceField: "",
          DisplayLookUp: ""
        },
        cValidate: "required",
        cName: "reason_code",
        cOrder: 1,
        cKey: false,
        cStatic: false,
        cProtect: false,
        cParentForm: "MM_FleetMaintenanceSchedule",
        cOption: [],
        cDisplayColumn: "",
        cInputStatus: this.inputStatus
      },
      PI_reason: {
        cValidate: "",
        cName: "reason",
        cOrder: 3,
        cKey: false,
        cProtect: false,
        cResize: false,
        cReadonly: false,
        cRows: 3,
        cMaxRows: 3,
        cSize: "md",
        cParentForm: "MM_FleetMaintenanceSchedule",
        cInputStatus: this.inputStatus,
      },
      scheduleHeader: [
        {
          key: "row_number",
          label: "No",
          tdClass: "ContentACCList2 notranslate",
          thClass: "HeaderACCList2"
        },
        {
          key: "fleet_no",
          label: "Fleet No",
          tdClass: "ContentACCList2 notranslate th-cus-left",
          thClass: "HeaderACCList2 S th-cus-center"
        },
        {
          key: "fleet_type",
          label: "Fleet Type",
          tdClass: "ContentACCList2 notranslate th-cus-center",
          thClass: "HeaderACCList2 S th-cus-center"
        },
        {
          key: "last_maint",
          label: "Last Maint",
          tdClass: "ContentACCList2 notranslate th-cus-center",
          thClass: "HeaderACCList2 S th-cus-center"
        },
        {
          key: "last_millage",
          label: "Last Millage",
          tdClass: "ContentACCList2 notranslate th-cus-center",
          thClass: "HeaderACCList2 S th-cus-center"
        },
        {
          key: "current_millage",
          label: "Current Millage",
          tdClass: "ContentACCList2 notranslate th-cus-center",
          thClass: "HeaderACCList2 S th-cus-center"
        },
        {
          key: "usage",
          label: "Usage",
          tdClass: "ContentACCList2 notranslate th-cus-center",
          thClass: "HeaderACCList2 S th-cus-center"
        },
        {
          key: "maint_type",
          label: "Maint Type",
          tdClass: "ContentACCList2 notranslate th-cus-center",
          thClass: "HeaderACCList2 th-cus-center",
        },
        {
          key: "status",
          label: "Status",
          tdClass: "ContentACCList2 notranslate th-cus-center",
          thClass: "HeaderACCList2 th-cus-center",
        },
      ],
    }
  },
  computed: {
    paramFromList() {
      var param = this.$store.getters.getParamPage;
      if (param == null || param == undefined) {
        this.doBack();
      } else {
        if (Object.keys(param).length < 1) {
          this.doBack();
        } else {
          return param;
        }
      }
    },
    ButtonStatus() {
      return this.$store.getters.getButtonStatus;
    },
  },
  methods: {
    doBack() {
      this.$router.go(-1);
    },
    GetDataBy() {
      let param = {
        option_url: "/OP/OP_FleetMaintSch",
        line_no: 0,
        id: this.paramFromList.row_id,
        lastupdatestamp: this.paramFromList.lastupdatestamp,
      };

      this.getJSON(this.getUrlCRUD(), param).then((response) => {
        // response from API
        if (response == null) return;

        this.Model = {
          ...this.Model,
          ...JSON.parse(response.Data[0].fmm_maintenance_fleet_sch_s)[0],
        }
        this.RenderData();
      });
    },
    OnMaintenanceStatusChange(data) {
      switch(this.Model.schedule_status) {
        case 'U':
        case 'S':
          this.PI_maintenance_date.cProtect = data == 'N';
          this.PI_maintenance_date.cValidate = data == 'N' ? '': 'required';

          this.Model.maintenance_date = data == 'N' ? '': this.Model.maintenance_date;
        break;
        case 'Q':
          this.PI_reason_code.cProtect = data == 'N';
          this.PI_reason_code.cValidate = data == 'N' ? '': 'required';
          this.PI_reason.cProtect = data == 'N';

          this.Model.reason_code = data == 'N' ? '': this.Model.reason_code;
          this.Model.reason_codeLabel = data == 'N' ? '': this.Model.reason_codeLabel;
          this.Model.reason = data == 'N' ? '': this.Model.reason;
        break;
        case 'P':
        break;
      }
    },
    OnMaintenanceDate(data) {
    },
    rowClicked(record, index) {
      var param = record;
      param.isEdit = true;
      param.isView = true;
      this.$store.commit("setParamPage", param);
      // this.$router.push({ name: "MM_FleetMaintenanceScheduleForm" });
    },
    RenderData() {
      switch(this.Model.schedule_status) {
        case 'U':
          this.titleSchedule = 'Schedule For Maintenance';
          this.titleDate = 'Schedule Date';
          this.Model.maintenance_status = 'Y';
        break;
        case 'S':
          this.titleSchedule = 'Cancel Schedule';
          this.titleDate = 'Reschedule Date';
          this.Model.maintenance_status = 'N';
        break;
        case 'Q':
          this.titleSchedule = 'Cancel Queuing';
          this.Model.maintenance_status = 'N';
        break;
        case 'P':
        break;
      }
      this.detail_days = [];
      if (this.Model.detail_day1) {
        this.detail_days.push(this.Model.detail_day1);
      }
      if (this.Model.detail_day2) {
        this.detail_days.push(this.Model.detail_day2);
      }
      if (this.Model.detail_day3) {
        this.detail_days.push(this.Model.detail_day3);
      }
    },
    doViewAll(index) {
      let param = this.paramFromList;
      param.indexDate = index;
      this.$store.commit("setParamPage", param);
      this.$router.push({ name: "MM_FleetMaintenanceScheduleDetailViewAll" });
    },
    OnReasonCodeChange(data) {
      this.$nextTick(() => {
        this.Model.reason_code = data.id;
        this.Model.reason_codeLabel = data.label;
        this.Model.reason = data.descs;
      });
    },
    doSave() {
      this.$validator._base.validateAll("MM_FleetMaintenanceSchedule").then((result) => {
        if (!result) return;
        this.alertConfirmation("Are You Sure Want To Save This Data ?").then(
          (ress) => {
            if (ress.value) {
              this.$validator.errors.clear("MM_FleetMaintenanceSchedule");
                this.M_Update();
            }
          }
        );
      });
    },
    M_Update() {
      let param = {
        option_url: "/OP/OP_FleetMaintSch",
        line_no: 0,

        mm_maintenance_fleet_sch_id: this.paramFromList.row_id,
        ss_portfolio_id: this.getDataUser().portfolio_id,
        schedule_status: this.Model.schedule_status,
        schedule_for_maintenance_status: this.Model.schedule_status == 'U' ? this.Model.maintenance_status: 'NULL',
        schedule_date: this.Model.schedule_status == 'U' && this.Model.maintenance_date != '' ? this.Model.maintenance_date: 'NULL',
        cancel_schedule_status: this.Model.schedule_status == 'S' ? this.Model.maintenance_status: 'NULL',
        reschedule_date: this.Model.schedule_status == 'S' && this.Model.maintenance_date != '' ? this.Model.maintenance_date: 'NULL',
        cancel_queuing_status: this.Model.schedule_status == 'Q' ? this.Model.maintenance_status: 'NULL',
        cancel_cm_reason_id: this.Model.schedule_status == 'Q' ? this.Model.reason_code: 'NULL',
        cancel_remarks: this.Model.schedule_status == 'Q' ? this.Model.reason: 'NULL',
        lastupdatestamp: this.paramFromList.lastupdatestamp,
        user_edit: this.getDataUser().user_id,
      };
      this.putJSON(this.getUrlCRUD(), param).then((response) => {
        if (response == null) return;
        this.doBack();
      });
    },
  },
  mounted() {
    this.GetDataBy();
  },
};
</script>